<template>
    <div>
        <body>
<div>
	<p class="luck"><img class="lucky" src="./images/luck.svg"></p>
</div>
<div class="lottery-box" id="app">
	<h1 class="title"></h1>
	<div class="lottery">
		<div class="lottery-item">
			<div class="lottery-start">
				<div class="box gray" v-if="isStart===0">
					<p></p>
				</div>
				<div class="box" @click.once="startLottery" v-if="isStart===1">
					<p><b></b></p>
				</div>
				<div class="box gray" v-if="isStart===2">
					<p></p>
				</div>
			</div>
			<ul>
				<li v-for="(item,i) in list" :class="i==index?'on':''" :key="item">
					<div class="box">
							<p>{{item.title}}</p>
							<p><img src="./images/j1.png" alt=""></p>
					</div>
				</li>
			</ul>
		</div>
	</div>  
	<!--  -->
	<div class="mask" v-if="showToast"></div>
	<div class="lottery-alert" v-if="showToast">
		<h1></h1>
		<p><img src="./images/j2.png" alt=""></p>
		<h2> {{list[index].title}}</h2>
		<div class="btnsave" @click="showToast=false"></div> 
	</div>  
</div>
<div class="bottomtext">
<div v-if ="coupon1" class="container box2 h4"><p class="midtext"><b class="midtext2"></b><br><br><b class="midtext3">- HappyDog -</b> <br><span class="bottext">9</span></p></div>
<div v-if ="coupon2" class="container box2 h4"><p class="midtext"><b class="midtext2"></b><br><br><b class="midtext3">- ElegantCat -</b> <br><span class="bottext">75</span></p></div>
<div v-if ="coupon3" class="container box2 h4"><p class="midtext"><b class="midtext2"></b><br><br><b class="midtext3">- LuckyRabbit -</b> <br><span class="bottext">5</span></p></div>
<div v-if ="coupon4" class="container box2 h4"><p class="midtext"><b class="midtext2"></b><br><br><b class="midtext3">- SleepingMouse -</b><br><span class="bottext">3</span></p></div>
<div v-if ="coupon5" class="container box2 h4"><p class="midtext4"><br></p></div>
</div>

</body>
        <!-- <loading :active.sync="isLoading"></loading>
    <div class="mt-5" v-if="showToast">
        <div class="text-center" >
            <div class="h3"></div>
            <hr>
            <div class="container box h4 box"><p class="midtext"><br><br>- HappyDog - <br><span class="bottext">5</span></p></div>           
            <div class="container box h4 box"><p class="midtext"><br><br>- LuckyCat - <br><span class="bottext">9</span></p></div>
        </div>
    </div> -->


</div>
</template>
<script>
export default {
    data(){
        return{
	  isStart:1,	
	  score:10, //
      list:[
	  	{img:'./images/j2.png',title:'',number:1},
		{img:'img/j2.png',title:'',number:2},
		{img:'img/j1.png',title:'',number:3},
		{img:'img/j2.png',title:'',number:4},
		{img:'img/j1.png',title:'',number:5},
		{img:'img/j2.png',title:'',number:6},
		{img:'img/j1.png',title:'',number:7},
		{img:'img/j2.png',title:'',number:8},
	  ],   //1-9     
      index: -1,  // 
      count: 8,  // 
      timer: 0,  // 
      speed: 200,  // 
      times: 0,    // 
      cycle: 50,   // 
      prize: -1,   // 
      click: true,
	  showToast: false, //
	  coupon1:false, 
	  coupon2:false,
	  coupon3:false,
	  coupon4:false,       
    };
    },
    mounted(){},
	
	methods:{
		startLottery(){
			if (!this.click) { return }
			this.startRoll();
			this.couponcode(); 
		},		
		// 
		startRoll () {
			this.times += 1 // 
			this.oneRoll() //  
			//  && 
			if (this.times > this.cycle + 10 && this.prize === this.index) {
			  clearTimeout(this.timer)  // 
			  this.prize = -1
			  this.times = 0
			  this.speed = 200
			  this.click = true; 
			  var that = this;
			  setTimeout(res=>{
				this.showToast = true;
			  },500)			                  
			} else {
			  if (this.times < this.cycle) {
				this.speed -= 10  // 
			  } else if (this.times === this.cycle) { 
				const index = parseInt(Math.random() * 10, 0) || 0;  // 
				  this.prize = index; //,
			if(this.list[index].number == 1){//
                setTimeout(res=>{
				this.coupon1 = true;
			},4000)
			}
			else if(this.list[index].number == 3){
				setTimeout(res=>{
				this.coupon2 = true;
			},4000)
			}
			else if(this.list[index].number == 5){
				setTimeout(res=>{
				this.coupon3 = true;
			},4000)
			}
            else if(this.list[index].number == 7){
				setTimeout(res=>{
				this.coupon4 = true;
			},4000)
			}
			else if(this.list[index].number == 2 | this.list[index].number == 4 | this.list[index].number == 6 | this.list[index].number == 8){
				setTimeout(res=>{
				this.coupon5 = true;
			},4000)
			}
				if (this.prize > 8) { this.prize = 8 }
			  } else if (this.times > this.cycle + 10 && ((this.prize === 0 && this.index === 8) || this.prize === this.index + 1)) {
				this.speed += 110
			  } else {
				this.speed += 20
			  }      
			  if (this.speed < 40) {this.speed = 40}
			  this.timer = setTimeout(this.startRoll, this.speed)
			}
		},

		// 
		oneRoll () {
		  let index = this.index // 
		  const count = this.count // 
		  index += 1
		  if (index > count - 1) { index = 0 }
		  this.index = index
		},
		couponcode(){
			if(this.list[index].number = 1){
                this.coupon = true;
			}
		},
	}	
}
</script>
<style lang="css" scoped>
@import "../../assets/lottery";
</style>



